$man-width: 40rem;
$font-size: 12px;
$subtext-width: $man-width - 4rem;
$delta-width: 3rem;
$subtext-left-margin: 3rem;
$delta-indent: $delta-width/2; // delta vertical indent between diff subtext levels

// responsive media queries
$screen-xs-min: 356px;
$screen-sm-min: 576px; // small tablets and large smartphones (landscape view)

@mixin xs {
  @media (max-width: #{$screen-xs-min}) {
    @content;
  }
}

@mixin sm {
  @media (max-width: #{$screen-sm-min}) {
    @content;
  }
}

body {
  font-family: monospace;
  font-size: $font-size;
  color: white;
  background-color: black;
  margin: 1rem 0.5rem 0 1rem;
  text-align: left;
}

// man content container
.mancontent {
  max-width: $man-width - 1rem;
  padding-right: 1rem;
}

// always has 3 spans
.manheader {
  margin: 0.5rem 0;
  height: 1rem;
  position: relative;
  max-width: $man-width;

  span {
    position: absolute;
  }

  span:nth-child(1) {
    left: 0;
  }

  span:nth-child(2) {
    left: 40%;
  }

  span:nth-child(3) {
    right: 0;
  }
}

h1 {
  font-size: $font-size;
  font-weight: bold;
  margin: 15px 0 0 0;
}

p {
  margin: 0 0 10px 0;
}

.subtext {
  margin-left: $subtext-left-margin;
  max-width: $subtext-width;
  @include sm {
    $subtext-left-margin: $subtext-left-margin / 3;
    margin-left: $subtext-left-margin;
    max-width: $subtext-width;
  }
}

.doublesubtext {
  margin-left: $subtext-left-margin + $delta-indent;
  max-width: $subtext-width - $delta-width;

  @include sm {
    $subtext-left-margin: $subtext-left-margin / 3;
    $delta-indent: $delta-indent / 1.5;
    $delta-width: $delta-width / 1.5;
    margin-left: $subtext-left-margin + $delta-indent;
    max-width: $subtext-width - $delta-width;
  }
}

.triplesubtext {
  margin-left: $subtext-left-margin + 2*$delta-indent;
  max-width: $subtext-width - 2*$delta-width;

  @include sm {
    $subtext-left-margin: $subtext-left-margin / 3;
    $delta-indent: $delta-indent / 1.5;
    $delta-width: $delta-width / 1.5;
    margin-left: $subtext-left-margin + 2*$delta-indent;
    max-width: $subtext-width - 2*$delta-width;
  }
}

.bottomcut {
  margin-bottom: 0;
}

a, a.active, a.visited, a.hover {
  color: white;
  text-decoration: underline;
}

.footer {
  margin: 0.5rem 0 1.5rem 0;
  color: #666;
}

.linkfloater {
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0.5rem;
  text-align: right;
  background-color: #222;
  color: #FFF;

  @include sm {
    // TODO make linkfloater responsive
    display: none;
  }

  a:link, a:visited, a:hover {
    color: #00FF00;
    text-decoration: underline;
  }
}
